<template>
  <div class="user-wrap">
    <van-nav-bar
      title="意见反馈"
      left-text="返回"
      left-arrow
      @click-left="onClickLeft"
    />
    <van-form @submit="onSubmit">
      <van-field
        v-model="message"
        rows="3"
        autosize
        label="意见反馈"
        type="textarea"
        maxlength="150"
        placeholder="请输入留言"
        show-word-limit
      />
      <div style="margin: 16px;">
        <van-button round block type="primary" native-type="submit">
          确定
        </van-button>
      </div>
    </van-form>
  </div>
</template>

<script>
export default {
  name: 'callback',
  components: {
  },
  data () {
    return {
      message: ''
    }
  },
  computed: {
  },
  mounted () {
  },
  methods: {
    onClickLeft () {
      this.$router.push({ path: '/user' })
    },
    onSubmit(values) {
      console.log('submit', values);
    }
  }
}
</script>



